import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, Observable, of as observableOf } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { FlatTreeControl } from '@angular/cdk/tree';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';

import { Settings } from '../../../shared/models/settings';
import { RTLService } from '../../../shared/services/rtl.service';
import { LoggerService } from '../../../shared/services/logger.service';
import { GetInfo } from '../../../shared/models/getInfo';
import { MenuNode, FlatMenuNode, MENU_DATA } from '../../../shared/models/navMenu';

@Component({
  selector: 'rtl-side-navigation',
  templateUrl: './side-navigation.component.html',
  styleUrls: ['./side-navigation.component.scss']
})
export class SideNavigationComponent implements OnInit, OnDestroy {
  public settings: Settings;
  public information: GetInfo = {};
  public flgLoading = true;
  private unsubscribe = [new Subject(), new Subject()];
  treeControl: FlatTreeControl<FlatMenuNode>;
  treeFlattener: MatTreeFlattener<MenuNode, FlatMenuNode>;
  dataSource: MatTreeFlatDataSource<MenuNode, FlatMenuNode>;

  constructor(private rtlService: RTLService, private logger: LoggerService) {
    this.settings = this.rtlService.getUISettings();

    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel, this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<FlatMenuNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    this.dataSource.data = MENU_DATA.children;
  }

  ngOnInit() {
    this.information = this.rtlService.readInformation();
    if (undefined !== this.information.currency_unit) {
      this.flgLoading = false;
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.unsubscribe[0]))
    .subscribe(data => {
      this.flgLoading = false;
      this.information = data;
      this.logger.info('Server Information Updated');
      this.logger.info(this.information);
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsubscribe[1]))
    .subscribe(settings => {
      this.settings = settings;
      this.logger.info(this.settings);
    });
  }

  private transformer(node: MenuNode, level: number) { return new FlatMenuNode(!!node.children, level, node.name, node.icon, node.link); }

  private getLevel(node: FlatMenuNode) { return node.level; }

  private isExpandable(node: FlatMenuNode) { return node.expandable; }

  private getChildren(node: MenuNode): Observable<MenuNode[]> { return observableOf(node.children); }

  hasChild(_: number, _nodeData: FlatMenuNode) { return _nodeData.expandable; }

  ngOnDestroy() {
    this.unsubscribe.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
