import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { Settings } from '../../shared/models/settings';
import { LoggerService } from '../../shared/services/logger.service';
import { AlertMessageComponent } from '../../shared/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../shared/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-server-config',
  templateUrl: './server-config.component.html',
  styleUrls: ['./server-config.component.scss']
})
export class ServerConfigComponent implements OnInit, OnDestroy {
  public settings: Settings;
  private prevConfigPath = '';
  public lndConfigData = [];
  private unsubConfig: Array<Subject<void>> = [new Subject(), new Subject()];

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    this.settings = {...this.rtlService.getUISettings()};
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsubConfig[0]))
    .subscribe(settings => {
      this.settings = {...settings};
      this.prevConfigPath = settings.lndConfigPath;
    });
  }

  onShowConfig() {
    this.lndConfigData = [];
    const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Opening Config File...'}});
    this.rtlService.fetchLNDServerConfig(this.settings.lndConfigPath)
    .pipe(takeUntil(this.unsubConfig[1]))
    .subscribe((data: string) => {
      dialogRef.close();
      this.lndConfigData = data.split('\n');
      this.logger.info(this.lndConfigData);
      if (this.settings.lndConfigPath.trim() !== this.prevConfigPath.trim()) {
        this.rtlService.updateUISettings(this.settings);
      }
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: JSON.stringify(err.error.error)}});
      this.logger.error(err);
    });
  }

  resetData() {
    this.lndConfigData = [];
    this.settings.lndConfigPath = '';
    if (this.settings.lndConfigPath.trim() !== this.prevConfigPath.trim()) {
      this.rtlService.updateUISettings(this.settings);
    }
  }

  ngOnDestroy() {
    this.unsubConfig.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
