import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DecimalPipe } from '@angular/common';

import { MatDialog } from '@angular/material';
import { RTLService } from '../../../shared/services/rtl.service';
import { LoggerService } from '../../../shared/services/logger.service';
import { GetInfo } from '../../../shared/models/getInfo';
import { Settings } from '../../../shared/models/settings';
import { AlertMessageComponent } from '../../../shared/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../../shared/components/spinner-dialog/spinner-dialog.component';
import { ConfirmationMessageComponent } from '../../../shared/components/confirmation-message/confirmation-message.component';

@Component({
  selector: 'rtl-send-payment',
  templateUrl: './send-payment.component.html',
  styleUrls: ['./send-payment.component.scss']
})
export class SendPaymentComponent implements OnInit, OnDestroy {
  public settings: Settings;
  public information: GetInfo = {};
  public paymentDecoded: any;
  public paymentRequest = '';
  private unsub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject(), new Subject()]; // 0: Info, 1: settings, 2: decodePayment, 3: sendPayment

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    this.settings = {...this.rtlService.getUISettings()};
    this.information = this.rtlService.readInformation();
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if(undefined === this.information.currency_unit) {
        this.rtlService.getInfo();
      }
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.unsub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsub[1]))
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  onSendPayment(form: any) {
    this.rtlService.decodePayment(this.paymentRequest)
    .pipe(takeUntil(this.unsub[2]))
    .subscribe((data: any) => {
      this.paymentDecoded = data;
      this.logger.info(data);
      const decimalPipe = new DecimalPipe('en-US');
      const confirmationMsg = 'Destination \t\t\t : ' + this.paymentDecoded.destination + '\nAmount (' +
      ((undefined === this.information.smaller_currency_unit) ? 'Sats' :  this.information.smaller_currency_unit) + ') \t\t : ' +
      decimalPipe.transform(this.paymentDecoded.num_satoshis) + '\nDescription \t\t\t : ' + this.paymentDecoded.description +
      '\nTimestamp \t\t\t : ' + this.paymentDecoded.timestamp_str + '\nExpiry \t\t\t\t : ' + this.paymentDecoded.expiry;
      const confirmDialog = this.dialog.open(ConfirmationMessageComponent,
        { width: '760px', data: {
          type: 'CONFIRM',
          message: confirmationMsg
        }
      });
      confirmDialog.afterClosed().subscribe(confirmResponse => {
        if (confirmResponse) {
          const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Sending Payment...'}});
          this.rtlService.sendPayment(this.paymentRequest)
          .pipe(takeUntil(this.unsub[3]))
          .subscribe((ResData: any) => {
            dialogRef.close();
            if (ResData.payment_error) {
              dialogRef.close();
              this.dialog.open(AlertMessageComponent, { data: {type: 'ERROR', message: ResData.payment_error}});
              this.logger.error('Error: ' + ResData.payment_error);
            } else {
              this.paymentDecoded = undefined;
              this.paymentRequest = undefined;
              form.resetForm();
              this.logger.info(ResData);
              this.dialog.open(AlertMessageComponent, { width: '760px', data: { type: 'SUCCESS', message: 'Payment Sent Successfully!\n\n' +
              'Total Fee (' + ((undefined === this.information.smaller_currency_unit) ? 'Sats' : this.information.smaller_currency_unit) +
              ') \t\t : ' + decimalPipe.transform((ResData.payment_route.total_fees_msat / 1000), '1.3-3') + '\n' + confirmationMsg}});
            }
          },
          (err: any) => {
            dialogRef.close();
            this.dialog.open(AlertMessageComponent, { data: {type: 'ERROR', message: err.error.error}});
            this.logger.error(err);
          });
        }
      });
    },
    (err: any) => {
      this.dialog.open(AlertMessageComponent, { data: {type: 'ERROR', message: 'Error In Decode Payment: ' + err.error.error}});
      this.logger.error(err);
    });
  }

  onVerifyPayment() {
    const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Decoding Payment...'}});
    this.rtlService.decodePayment(this.paymentRequest)
    .pipe(takeUntil(this.unsub[2]))
    .subscribe((data: any) => {
      dialogRef.close();
      this.paymentDecoded = data;
      this.logger.info(data);
    },
    (err: any) => {
      dialogRef.close();
      this.paymentRequest = '';
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err);
    });
  }

  resetData() {
    this.paymentRequest = '';
    this.paymentDecoded = undefined;
  }

  ngOnDestroy() {
    this.unsub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
