import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Subject, of, forkJoin } from 'rxjs';
import { map } from 'rxjs/operators';

import { environment } from '../../../environments/environment';
import { LoggerService } from './logger.service';
import { Settings } from '../models/settings';
import { GetInfo } from '../models/getInfo';
import { AddressType } from '../models/address';
import { Transactions } from '../../shared/models/transactions';
import { Peer } from '../models/peers';

@Injectable()
export class RTLService {
  private settings = new Settings(true, true, 'horizontal', 'default', 'blue-dark', '', false);
  private information: GetInfo = {};
  private addressTypes: AddressType[] = [];
  private peers: Peer[] = [];

  UIsettingsUpdated = new Subject<Settings>();
  informationUpdated = new Subject<any>();

  constructor(private http: HttpClient, private logger: LoggerService) {
    this.setAddressType({ addressId: '0', addressTp: 'p2wkh', addressDetails: 'Pay to witness key hash'});
    this.setAddressType({ addressId: '1', addressTp: 'np2wkh', addressDetails: 'Pay to nested witness key hash (default)'});
  }

  readInformation() {
    return this.information;
  }

  getInfo() {
    this.http.get(environment.GETINFO_API)
    .subscribe((data: any) => {
      this.information = data;
      if (undefined !== this.information.chains) {
        this.information.smaller_currency_unit = (this.information.chains[0].toLowerCase().indexOf('bitcoin') < 0) ? 'Litoshis' : 'Sats';
        this.information.currency_unit = (this.information.chains[0].toLowerCase().indexOf('bitcoin') < 0) ? 'LTC' : 'BTC';
      } else {
        this.information.smaller_currency_unit = 'Sats';
        this.information.currency_unit = 'BTC';
      }
      this.information.version = (undefined === this.information.version) ? '' : this.information.version.split(' ')[0];
      this.logger.info(this.information);
      this.informationUpdated.next(this.information);
    },
    (err: any) => {
      this.informationUpdated.next(err);
    });
  }

  decodePayment(routeParam: string) {
    return this.http.get(environment.PAYREQUEST_API + '/' + routeParam);
  }

  getChannels(routeParam: string, channelStatus: string) {
    const options = (undefined === channelStatus || channelStatus === '') ? {} : { params: new HttpParams().set(channelStatus, 'true') };
    return this.http.get(environment.CHANNELS_API + '/' + routeParam, options);
  }

  addChannel(pubkey: string, fundingAmt: number) {
    return this.http.post(environment.CHANNELS_API, {node_pubkey: pubkey, local_funding_amount: fundingAmt});
  }

  closeChannel(channelPoint: string, forcibly: boolean) {
    return this.http.delete(environment.CHANNELS_API + '/' + channelPoint + '?force=' + forcibly);
  }

  sendPayment(sendPaymentReq: string) {
    return this.http.post(environment.CHANNELS_API + '/transactions', {paymentReq: sendPaymentReq});
  }

  getPeers() {
    if (this.peers.length > 0) {
      return of(this.peers);
    } else {
      return this.http.get(environment.PEERS_API)
      .pipe(map((data: Peer[]) => {
        this.peers = data;
        this.peers.forEach((peer, idx) => {
          forkJoin(
            this.http.get(environment.NETWORK_API + '/node/' + peer.pub_key)
          )
          .subscribe(([res]: any[]) => {
            this.peers[idx].alias = res.node.alias;
          });
        });
        return this.peers;
      }));
    }
  }

  addPeer(pubkey: string, host: string) {
    return this.http.post(environment.PEERS_API, {pubkey: pubkey, host: host, perm: false});
  }

  getAddressTypes() {
    return this.addressTypes;
  }

  setAddressType(type) {
    this.addressTypes.push(type);
  }

  getPaymentsList() {
    return this.http.get(environment.PAYMENTS_API);
  }

  getFees() {
    return this.http.get(environment.FEES_API);
  }

  getNewAddress(selAddr: AddressType) {
    return this.http.get(environment.NEW_ADDRESS_API + '?type=' + selAddr.addressId);
  }

  setTransactions(trans: Transactions) {
    return this.http.post(environment.TRANSACTIONS_API, {amount: trans.amount, address: trans.address, fees: trans.fees, blocks: trans.blocks });
  }

  getBalance(routeParam: string) {
    if (undefined === routeParam || null === routeParam) {
      routeParam = '';
    }
    return this.http.get(environment.BALANCE_API + '/' + routeParam);
  }

  operateWallet(operation: string, pwd: string) {
    return this.http.post(environment.WALLET_API + '/' + operation, { wallet_password: pwd });
  }

  getNetworkInfo() {
    return this.http.get(environment.NETWORK_API + '/info');
  }

  getGraphNode(pubkey: string) {
    return this.http.get(environment.NETWORK_API + '/node/' + pubkey);
  }

  fetchLNDServerConfig(filePath: string) {
    const filePathHeader = new HttpHeaders({ filePath: filePath });
    return this.http.get(environment.LND_SERVER_CONFIG_API, {headers: filePathHeader});
  }

  getUISettings() {
    return this.settings;
  }

  setUISettings(newSettings: Settings) {
    this.settings = newSettings;
    this.UIsettingsUpdated.next(this.settings);
  }

  fetchUISettings() {
    this.http.get(environment.UI_SETTINGS_API)
    .subscribe((data: any) => {
      this.settings = data.settings;
      this.logger.info(this.settings);
      this.UIsettingsUpdated.next(this.settings);
    });
  }

  updateUISettings(settings: Settings) {
    this.logger.info('Updated Settings:');
    this.logger.info(settings);
    this.http.post(environment.UI_SETTINGS_API, { updatedSettings: settings })
    .subscribe((data: any) => {
      this.logger.info(data);
      this.settings = settings;
      this.UIsettingsUpdated.next(settings);
    },
    (err: any) => {
      this.logger.error('UI Settings Updation Failed!');
    });
  }

}
