import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { RTLService } from '../../shared/services/rtl.service';
import { LoggerService } from '../../shared/services/logger.service';
import { GetInfo } from '../../shared/models/getInfo';
import { NetworkInfo } from '../../shared/models/networkInfo';
import { Fees } from '../../shared/models/fees';
import { Settings } from '../../shared/models/settings';

@Component({
  selector: 'rtl-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, OnDestroy {
  public settings: Settings;
  public fees: Fees;
  public information: GetInfo = {};
  public activeChannels = 0;
  public remainder = 0;
  public totalBalance = '';
  public channelBalance = '';
  public BTCtotalBalance = '';
  public BTCchannelBalance = '';
  public networkInfo: NetworkInfo = {};
  public flgLoading: Array<Boolean | 'error'> = [true, true, true, true, true]; // 0: Info, 1: Fee, 2: Wallet, 3: Channel, 4: Network
  private unsub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject(), new Subject(), new Subject(), new Subject(), new Subject()];
                                          // 0: Info, 1: Fee, 2: Wallet, 3: Channel, 4: Network, 5: Settings

  constructor(private rtlService: RTLService, private logger: LoggerService, private router: Router) {}

  ngOnInit() {
    this.settings = {...this.rtlService.getUISettings()};
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.flgLoading[0] = true;
        this.rtlService.getInfo();
      } else {
        this.flgLoading[0] = false;
      }
    } else {
      this.flgLoading[0] = false;
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.unsub[0]))
    .subscribe((data: any) => {
      this.flgLoading[0] = false;
      if (data.error) {
        this.logger.info('Redirecting to Unlock');
        this.router.navigate(['/start']);
        return;
      }
      this.logger.info(data);
      this.information = data;
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[0] = 'error';
    });
    this.rtlService.getFees()
    .pipe(takeUntil(this.unsub[1]))
    .subscribe((fees: any) => {
      this.flgLoading[1] = false;
      this.fees = (undefined === fees) ? {} : fees;
      this.logger.info(this.fees);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[1] = 'error';
    });
    this.rtlService.getBalance('blockchain')
    .pipe(takeUntil(this.unsub[2]))
    .subscribe((data: any) => {
      this.flgLoading[2] = false;
      this.totalBalance = (undefined === data.total_balance) ? 0 : data.total_balance;
      this.BTCtotalBalance = (undefined === data.btc_total_balance) ? 0 : data.btc_total_balance;
      this.logger.info(this.totalBalance);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[2] = 'error';
    });
    this.rtlService.getBalance('channels')
    .pipe(takeUntil(this.unsub[3]))
    .subscribe((data: any) => {
      this.flgLoading[3] = false;
      this.channelBalance = (undefined === data.balance) ? 0 : data.balance;
      this.BTCchannelBalance = (undefined === data.btc_balance) ? 0 : data.btc_balance;
      this.logger.info(this.channelBalance);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[3] = 'error';
    });
    this.rtlService.getNetworkInfo()
    .pipe(takeUntil(this.unsub[4]))
    .subscribe((data: any) => {
      this.flgLoading[4] = false;
      this.networkInfo = (undefined === data) ? {} : data;
      this.logger.info(this.networkInfo);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[4] = 'error';
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsub[5]))
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  ngOnDestroy() {
    this.unsub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
