import { Component, OnInit, OnDestroy, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs/Subject';
import { takeUntil } from 'rxjs/operators';

import { Settings } from '../../../shared/models/settings';
import { LoggerService } from '../../../shared/services/logger.service';
import { RTLService } from '../../../shared/services/rtl.service';
import { GetInfo } from '../../../shared/models/getInfo';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'rtl-top-menu',
  templateUrl: './top-menu.component.html',
  styleUrls: ['./top-menu.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TopMenuComponent implements OnInit, OnDestroy {
  public settings: Settings;
  public version = '';
  private settingsSub = new Subject<void>();
  private infoSub = new Subject<void>();
  public information: GetInfo = {};
  public flgLoading = true;

  constructor(private rtlService: RTLService, private logger: LoggerService, private router: Router) {
    this.version = environment.VERSION;
  }

  ngOnInit() {
    this.information = this.rtlService.readInformation();
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.infoSub))
    .subscribe(data => {
      this.flgLoading = false;
      this.information = data;
      this.logger.info('Server Information Updated');
      this.logger.info(this.information);
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.settingsSub))
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  ngOnDestroy() {
    this.infoSub.next();
    this.infoSub.complete();
  }

}
