import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

import { NotFoundComponent } from './shared/components/not-found/not-found.component';

import { HomeComponent } from './pages/home/home.component';
import { GetStartedComponent } from './pages/get-started/get-started.component';
import { ChannelDashboardComponent } from './pages/channels/channel-dashboard/channel-dashboard.component';
import { ChannelManageComponent } from './pages/channels/channel-manage/channel-manage.component';
import { PeersComponent } from './pages/peers/peers.component';
import { WalletComponent } from './pages/wallet/wallet.component';
import { ListPaymentsComponent } from './pages/payments/list-payments/list-payments.component';
import { SendPaymentComponent } from './pages/payments/send-payment/send-payment.component';
import { ServerConfigComponent } from './pages/server-config/server-config.component';
import { HelpComponent } from './pages/help/help.component';

export const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full'},
  { path: 'start', component: GetStartedComponent },
  { path: 'home', component: HomeComponent },
  { path: 'peers', component: PeersComponent },
  { path: 'chnldashboard', component: ChannelDashboardComponent },
  { path: 'chnlmanage', component: ChannelManageComponent },
  { path: 'wallet', component: WalletComponent },
  { path: 'listpayments', component: ListPaymentsComponent },
  { path: 'sendpayment', component: SendPaymentComponent },
  { path: 'sconfig', component: ServerConfigComponent },
  { path: 'help', component: HelpComponent },
  { path: '**', component: NotFoundComponent }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes, {});
