import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { RTLService } from '../../../shared/services/rtl.service';
import { Channel } from '../../../shared/models/channels';
import { Peer } from '../../../shared/models/peers';
import { GetInfo } from '../../../shared/models/getInfo';
import { LoggerService } from '../../../shared/services/logger.service';
import { AlertMessageComponent } from '../../../shared/components/alert-message/alert-message.component';
import { ConfirmationMessageComponent } from '../../../shared/components/confirmation-message/confirmation-message.component';
import { SpinnerDialogComponent } from '../../../shared/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-channel-manage',
  templateUrl: './channel-manage.component.html',
  styleUrls: ['./channel-manage.component.scss']
})
export class ChannelManageComponent implements OnInit, OnDestroy {
  @ViewChild(MatSort) sort: MatSort;
  public position = 'below';
  public selectedPeer = '';
  public fundingAmount: number;
  public displayedColumns = [
    'close', 'active', 'chan_id', 'remote_pubkey', 'capacity', 'local_balance', 'remote_balance',
    'total_satoshis_sent', 'total_satoshis_received', 'commit_fee'
  ];
  public channels: any;
  public peers: Peer[] = [];
  public information: GetInfo = {};
  public flgLoading: Array<Boolean | 'error'> = [true, true, true, true];
  public selectedFilter = '';
  public statusFilters = ['Active', 'Inactive'];
  // public statusFilters = ['Active', 'Inactive', 'Pending'];
  private channelSub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject(), new Subject(), new Subject()];

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.flgLoading[0] = true;
        this.rtlService.getInfo();
      } else {
        this.flgLoading[0] = false;
      }
    } else {
      this.flgLoading[0] = false;
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.channelSub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.rtlService.getPeers()
    .pipe(takeUntil(this.channelSub[1]))
    .subscribe((data: any[]) => {
      this.flgLoading[0] = false;
      this.peers = data;
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[0] = 'error';
    });
    this.loadChannelsTable();
  }

  onOpenChannel(form: any) {
    const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Opening Channel...'}});
    // let SatsFundingAmount = this.rtlService.convertBTCToSats(this.fundingAmount);
    this.rtlService.addChannel(this.selectedPeer, this.fundingAmount)
    .subscribe(data => {
      dialogRef.close();
      this.selectedPeer = '';
      this.fundingAmount = null;
      form.resetForm();
      this.logger.info(data);
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Channel Added Successfully!'}});
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err);
    });
  }

  onChannelClose(channelToClose: Channel) {
    const confirmationMsg = 'Closing channel: ' + channelToClose.chan_id;
    const confirmDialog = this.dialog.open(ConfirmationMessageComponent,
      { width: '760px', data: {
        type: 'CONFIRM',
        message: confirmationMsg
      }
    });
    confirmDialog.afterClosed().subscribe(confirmResponse => {
      if (confirmResponse) {
        const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Closing Channel...'}});
        this.rtlService.closeChannel(channelToClose.channel_point, false)
        .subscribe(resData => {
          dialogRef.close();
          this.logger.info(resData);
          this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Channel Closed Successfully!'}});
          this.loadChannelsTable();
        },
        (err: any) => {
          dialogRef.close();
          if (
              err.error.error.grpc_code === 2
              && err.error.error.http_code === 500
              && err.error.error.message.indexOf('unable to gracefully close channel') >= 0
            ) {
            const confirmMsg = 'Unable to gracefully close channel while peer is offline.\nForce close the channel?';
            const confirmDlg = this.dialog.open(ConfirmationMessageComponent,
              { width: '760px', data: {
                type: 'ERROR',
                message: confirmMsg
              }
            });
            confirmDlg.afterClosed().subscribe(cnfrmRes => {
              if (cnfrmRes) {
                const dialogRefForce = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Closing Channel Forcibly...'}});
                this.rtlService.closeChannel(channelToClose.channel_point, true)
                .subscribe(resData => {
                  dialogRef.close();
                  this.logger.info(resData);
                  this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Channel Closed Forcibly!'}});
                  this.loadChannelsTable();
                },
                (errRes: any) => {
                  dialogRefForce.close();
                  if (
                    err.error.error.grpc_code === 2
                    && err.error.error.http_code === 500
                    && err.error.error.message.indexOf('unable to gracefully close channel') >= 0
                  ) {
                    err.error.error.message = 'Unable to close forcibly. Try again later.';
                  }
                  this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error.message}});
                  this.logger.error(err);
                });
              }
            });
          } else {
            this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error.message}});
            this.logger.error(err);
          }
        });
      }
    });
  }

  applyFilter(selFilter: string) {
    this.selectedFilter = selFilter;
    this.channels.filter = selFilter;
  }

  onChannelClick(selRow: Channel, event: any) {
    const flgCloseClicked = event.target.className.includes('mat-column-close') || event.target.className.includes('mat-icon');
    if (flgCloseClicked) {
      return;
    }
    const selChannel = this.channels.data.filter(channel => {
      return channel.chan_id === selRow.chan_id;
    });
    this.dialog.open(AlertMessageComponent, { width: '850px', data: {type: 'INFO', message: JSON.stringify(selChannel[0]), jsonMsg: true}});
  }

  loadChannelsTable() {
    this.rtlService.getChannels('all', '')
    .pipe(takeUntil(this.channelSub[2]))
    .subscribe((data: any) => {
      this.flgLoading[2] = false;
      if (undefined === data.channels) {
        data.channels = [];
      }
      data.channels.sort(function(a, b) {
        return (a.active === b.active) ? 0 : ((a.active) ? -1 : 1);
      });
      data.channels.filter(channel => {
        if (channel.active === true) {
          channel.active = 'Active';
        } else {
          channel.active = 'Inactive';
        }
      });
      this.channels = new MatTableDataSource<Channel>([...data.channels]);
      this.channels.sort = this.sort;
      this.channels.filterPredicate = (channel, selFilter) => {
        if (channel.active) {
          return channel.active.startsWith(selFilter);
        }
        return false;
      };
        this.logger.info(this.channels);
      },
      (err: Error) => {
        this.logger.error(err);
        this.flgLoading[2] = 'error';
    });
  }

  ngOnDestroy() {
    this.channelSub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
