import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { LoggerService } from '../../shared/services/logger.service';
import { AlertMessageComponent } from '../../shared/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../shared/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-get-started',
  templateUrl: './get-started.component.html',
  styleUrls: ['./get-started.component.scss']
})
export class GetStartedComponent implements OnInit, OnDestroy {
  walletPassword = '';
  private unsub = new Subject();

  constructor(private rtlService: RTLService, private logger: LoggerService, private router: Router, public dialog: MatDialog) {}

  ngOnInit() {
    this.walletPassword = '';
  }

  onOperateWallet(operation: string) {
    const dialogRefUnlock = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Unlocking...'}});
    this.rtlService.operateWallet(operation, this.walletPassword)
    .pipe(takeUntil(this.unsub))
    .subscribe((data: any) => {
      const dialogRefInit = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Initializing Node...'}});
      dialogRefUnlock.close();
      this.logger.info('Successfully Unlocked!');
      setTimeout(() => {
        this.walletPassword = '';
        this.logger.info('Successfully Initialized!');
        this.router.navigate(['/home']);
        dialogRefInit.close();
      }, 1000 * 90);
    },
    (err: any) => {
      this.walletPassword = '';
      dialogRefUnlock.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err.error.error);
    });
  }

  ngOnDestroy() {
    this.unsub.next();
    this.unsub.complete();
  }

}
