import { Component, OnInit, OnDestroy, ViewChild, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { MatTableDataSource, MatSort, MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { Peer } from '../../shared/models/peers';
import { GetInfo } from '../../shared/models/getInfo';
import { LoggerService } from '../../shared/services/logger.service';
import { AlertMessageComponent } from '../../shared/components/alert-message/alert-message.component';
import { SpinnerDialogComponent } from '../../shared/components/spinner-dialog/spinner-dialog.component';

@Component({
  selector: 'rtl-peers',
  templateUrl: './peers.component.html',
  styleUrls: ['./peers.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PeersComponent implements OnInit, OnDestroy {
  @ViewChild(MatSort) sort: MatSort;
  public position = 'above';
  public displayedColumns = ['pub_key', 'alias', 'address', 'bytes_sent', 'bytes_recv', 'sat_sent', 'sat_recv', 'inbound', 'ping_time'];
  public peerAddress = '';
  public peers: any;
  public information: GetInfo = {};
  private peersJSONArr: Peer[] = [];
  public flgLoading: Array<Boolean | 'error'> = [true]; // 0: peers
  private peersSub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject(), new Subject()]; // 0: info, 1: getPeers, 2: addPeers, 3: getGraphNode

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.flgLoading[0] = true;
        this.rtlService.getInfo();
      } else {
        this.flgLoading[0] = false;
      }
    } else {
      this.flgLoading[0] = false;
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.peersSub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.rtlService.getPeers()
    .pipe(takeUntil(this.peersSub[1]))
    .subscribe((peers: Peer[]) => {
      this.flgLoading[0] = false;
      this.peersJSONArr = peers;
      this.peers = (undefined === peers) ?  new MatTableDataSource([]) : new MatTableDataSource<Peer>([...this.peersJSONArr]);
      this.peers.data = this.peersJSONArr;
      this.peers.sort = this.sort;
      this.logger.info(this.peers);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[0] = 'error';
    });
  }

  onAddPeer(form: any) {
    const pattern = '^([a-zA-Z0-9]){1,66}@(([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([0-9]|[1-9][0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5]):[0-9]+$';
    const deviderIndex = this.peerAddress.search('@');
    let pubkey = '';
    let host = '';

    if (new RegExp(pattern).test(this.peerAddress)) {
      pubkey = this.peerAddress.substring(0, deviderIndex);
      host = this.peerAddress.substring(deviderIndex + 1);
      this.addPeerWithParams(form, pubkey, host);
    } else {
      const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Getting Node Address...'}});
      this.rtlService.getGraphNode(this.peerAddress)
      .pipe(takeUntil(this.peersSub[3]))
      .subscribe((data: any) => {
        dialogRef.close();
        host = (undefined === data.node.addresses || undefined === data.node.addresses[0].addr) ? '' : data.node.addresses[0].addr;
        this.addPeerWithParams(form, this.peerAddress, host);
      },
      (err: any) => {
        dialogRef.close();
        this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
        this.logger.error(err);
      });
    }
  }

  addPeerWithParams(form: any, pubkey: string, host: string) {
    const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Adding Peer...'}});
    this.rtlService.addPeer(pubkey, host)
    .pipe(takeUntil(this.peersSub[2]))
    .subscribe((data: any) => {
      dialogRef.close();
      this.peersJSONArr.push({pub_key: pubkey, address: host});
      this.peers.data = this.peersJSONArr;
      this.peerAddress = '';
      form.resetForm();
      this.logger.info(data);
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'SUCCESS', message: 'Peer Added Successfully!'}});
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: err.error.error}});
      this.logger.error(err);
    });
  }

  ngOnDestroy() {
    this.peersSub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }
}
