import { Component, OnInit, AfterViewInit, OnDestroy, ViewChild, HostListener } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { Settings } from './shared/models/settings';
import { RTLService } from './shared/services/rtl.service';
import { LoggerService } from './shared/services/logger.service';

@Component({
  selector: 'rtl-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('sideNavigation') sideNavigation: any;
  public settings: Settings;
  unsubscribe = new Subject();

  constructor(private rtlService: RTLService, private logger: LoggerService) {
    this.rtlService.fetchUISettings();
    this.settings = this.rtlService.getUISettings();
    if (window.innerWidth <= 768) {
      this.settings.menu = 'Vertical';
      this.settings.flgSidenavOpened = false;
      this.settings.flgSidenavPinned = false;
    }
  }

  ngOnInit() {
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsubscribe))
    .subscribe(settings => {
      this.settings = settings;
      if (window.innerWidth <= 768) {
        this.settings.menu = 'Vertical';
        this.settings.flgSidenavOpened = false;
        this.settings.flgSidenavPinned = false;
      }
      this.logger.info(this.settings);
    });
  }

  ngAfterViewInit() {
    if (!this.settings.flgSidenavPinned) {
      this.sideNavigation.close();
    }
    if (window.innerWidth <= 768) {
      this.sideNavigation.close();
    }
  }

  @HostListener('window:resize')
  public onWindowResize(): void {
    if (window.innerWidth <= 768) {
      this.settings.menu = 'Vertical';
      this.settings.flgSidenavOpened = false;
      this.settings.flgSidenavPinned = false;
    }
  }

  sideNavToggle() {
    this.sideNavigation.toggle();
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }
}
