import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DecimalPipe } from '@angular/common';

import { MatDialog } from '@angular/material';
import { RTLService } from '../../shared/services/rtl.service';
import { LoggerService } from '../../shared/services/logger.service';
import { GetInfo } from '../../shared/models/getInfo';
import { Blockchain } from '../../shared/models/blockchain';
import { Transactions } from '../../shared/models/transactions';
import { SpinnerDialogComponent } from '../../shared/components/spinner-dialog/spinner-dialog.component';
import { AlertMessageComponent } from '../../shared/components/alert-message/alert-message.component';
import { ConfirmationMessageComponent } from '../../shared/components/confirmation-message/confirmation-message.component';
import { AddressType } from '../../shared/models/address';
import { Settings } from '../../shared/models/settings';

@Component({
  selector: 'rtl-wallet',
  templateUrl: './wallet.component.html',
  styleUrls: ['./wallet.component.scss']
})
export class WalletComponent implements OnInit, OnDestroy {
  public settings: Settings;
  public addressTypes = [];
  public flgLoadingWallet: Boolean | 'error' = true;
  public selectedAddress: AddressType = {};
  public blockchainBalance: Blockchain = {};
  public information: GetInfo = {};
  public newAddress = '';
  public transaction: Transactions = {};
  private unsub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject(), new Subject(), new Subject()];
                                        // 0: Info, 1: blockchain, 2: getNewAddress, 3: setTransactions, 4: settings

  constructor(private rtlService: RTLService, private logger: LoggerService, public dialog: MatDialog) {}

  ngOnInit() {
    this.settings = {...this.rtlService.getUISettings()};
    this.addressTypes = this.rtlService.getAddressTypes();
    this.information = this.rtlService.readInformation();
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.rtlService.getInfo();
      }
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.unsub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.rtlService.getBalance('blockchain')
    .pipe(takeUntil(this.unsub[1]))
    .subscribe((data: any) => {
      this.flgLoadingWallet = false;
      this.blockchainBalance = data;
      if (undefined === this.blockchainBalance.total_balance) {
        this.blockchainBalance.total_balance = '0';
      }
      if (undefined === this.blockchainBalance.confirmed_balance) {
        this.blockchainBalance.confirmed_balance =  '0';
      }
      if (undefined === this.blockchainBalance.unconfirmed_balance) {
        this.blockchainBalance.unconfirmed_balance =  '0';
      }
    },
    (err: Error) => {
      this.flgLoadingWallet = 'error';
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsub[4]))
    .subscribe( settings => {
      this.settings = settings;
    });
  }

  onGenerateAddress() {
    const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Getting New Address...'}});
    this.rtlService.getNewAddress(this.selectedAddress)
    .pipe(takeUntil(this.unsub[2]))
    .subscribe((data: any) => {
      dialogRef.close();
      this.newAddress = data.address;
      this.logger.info(this.newAddress);
    },
    (err: any) => {
      dialogRef.close();
      this.dialog.open(AlertMessageComponent, { width: '700px', data: {type: 'ERROR', message: JSON.stringify(err.error.error)}});
      this.logger.error(err);
    });
  }

  onSendFunds(form: any) {
    const decimalPipe = new DecimalPipe('en-US');
    const confirmationMsg = 'BTC Address \t\t\t\t\t : ' + this.transaction.address + '\nAmount (' + this.information.smaller_currency_unit +
    ') \t\t\t\t\t : ' + decimalPipe.transform(this.transaction.amount) + '\nTarget Confirmation Blocks \t\t : ' + this.transaction.blocks +
    '\nFee (' + this.information.smaller_currency_unit + '/Byte) \t\t\t\t\t : ' + this.transaction.fees;
    const confirmDialog = this.dialog.open(ConfirmationMessageComponent,
      { height: '250px', width: '450px', data: {
        type: 'CONFIRM',
        message: confirmationMsg
      }
    });
    confirmDialog.afterClosed().subscribe(confirmResponse => {
      if (confirmResponse) {
        const dialogRef = this.dialog.open(SpinnerDialogComponent, { data: { message: 'Sending Funds...'}});
        this.rtlService.setTransactions(this.transaction)
        .pipe(takeUntil(this.unsub[3]))
        .subscribe((data: any) => {
          dialogRef.close();
          this.transaction = {};
          form.resetForm();
          this.logger.info(data);
          this.dialog.open(AlertMessageComponent, { data: {type: 'SUCCESS', message: 'Fund Sent Successfully!'}});
        },
        (err: any) => {
          dialogRef.close();
          this.dialog.open(AlertMessageComponent, { data: {type: 'ERROR', message: err.error.error}});
          this.logger.error(err);
        });
      }
    });
  }

  get invalidValues(): boolean {
    return undefined === this.transaction.address || this.transaction.address === ''
        || undefined === this.transaction.amount || this.transaction.amount <= 0
        || undefined === this.transaction.blocks || this.transaction.blocks <= 0
        || undefined === this.transaction.fees || this.transaction.fees <= 0;
  }

  ngOnDestroy() {
    this.unsub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }
}
