import { Component, OnInit, OnDestroy, ViewChild, ViewEncapsulation } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { MatTableDataSource, MatSort } from '@angular/material';
import { RTLService } from '../../../shared/services/rtl.service';
import { LoggerService } from '../../../shared/services/logger.service';
import { GetInfo } from '../../../shared/models/getInfo';
import { Payment } from '../../../shared/models/payment';
import { Settings } from '../../../shared/models/settings';

@Component({
  selector: 'rtl-list-payments',
  templateUrl: './list-payments.component.html',
  styleUrls: ['./list-payments.component.scss']
})
export class ListPaymentsComponent implements OnInit, OnDestroy {
  @ViewChild(MatSort) sort: MatSort;
  public position = 'above';
  public settings: Settings;
  public flgLoading: Array<Boolean | 'error'> = [true];
  public information: GetInfo = {};
  public payments: any;
  public paymentJSONArr: Payment[] = [];
  public displayedColumns = ['creation_date_str', 'payment_hash', 'fee', 'value_msat', 'value_sat', 'value', 'payment_preimage', 'path'];
  private unsub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject()]; // 0: Info, 1: settings, 2: paymentsList

  constructor(private rtlService: RTLService, private logger: LoggerService) {}

  ngOnInit() {
    this.settings = {...this.rtlService.getUISettings()};
    this.information = this.rtlService.readInformation();
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.rtlService.getInfo();
      }
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.unsub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.rtlService.UIsettingsUpdated
    .pipe(takeUntil(this.unsub[1]))
    .subscribe( settings => {
      this.settings = settings;
    });
    this.rtlService.getPaymentsList()
    .pipe(takeUntil(this.unsub[2]))
    .subscribe((payments: Payment[]) => {
      this.flgLoading[0] = false;
      this.paymentJSONArr = (payments.length > 0) ? payments : [];
      this.payments = (undefined === payments) ?  new MatTableDataSource([]) : new MatTableDataSource<Payment>([...this.paymentJSONArr]);
      this.payments.data = this.paymentJSONArr;
      this.payments.sort = this.sort;
      this.logger.info(this.payments);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[0] = 'error';
    });
}

  ngOnDestroy() {
    this.unsub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }

}
