import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { RTLService } from '../../../shared/services/rtl.service';
import { Peer } from '../../../shared/models/peers';
import { GetInfo } from '../../../shared/models/getInfo';
import { LoggerService } from '../../../shared/services/logger.service';

@Component({
  selector: 'rtl-channel-dashboard',
  templateUrl: './channel-dashboard.component.html',
  styleUrls: ['./channel-dashboard.component.scss']
})
export class ChannelDashboardComponent implements OnInit, OnDestroy {
  public channels: any;
  public position = 'below';
  public activeChannels = 0;
  public inactiveChannels = 0;
  public pendingChannels = 0;
  public peers: Peer[] = [];
  public information: GetInfo = {};
  public flgLoading: Array<Boolean | 'error'> = [true, true, true];
  private channelSub: Array<Subject<void>> = [new Subject(), new Subject(), new Subject()];
  localBal = 0;
  remoteBal = 0;
  maxBalanceValue = 0;
  totalLocalBalance: any[] = [{name: 'Local Balance', value: 1}];
  totalRemoteBalance: any[] = [{name: 'Remote Balance', value: 1}];
  view: any[] = [250, 400];
  yAxisLabel = 'Balance';
  colorScheme = {domain: ['#FFFFFF']};

  constructor(private rtlService: RTLService, private logger: LoggerService) {
    Object.assign(this, this.totalLocalBalance);
    Object.assign(this, this.totalRemoteBalance);
  }

  ngOnInit() {
    if (undefined === this.information.currency_unit) {
      this.information = this.rtlService.readInformation();
      if (undefined === this.information.currency_unit) {
        this.flgLoading[0] = true;
        this.rtlService.getInfo();
      } else {
        this.flgLoading[0] = false;
      }
    } else {
      this.flgLoading[0] = false;
    }
    this.rtlService.informationUpdated
    .pipe(takeUntil(this.channelSub[0]))
    .subscribe((data: any) => {
      this.information = data;
      this.logger.info(this.information);
    });
    this.activeChannels = 0;
    this.inactiveChannels = 0;
    this.rtlService.getChannels('all', '')
    .pipe(takeUntil(this.channelSub[1]))
    .subscribe((data: any) => {
      this.flgLoading[1] = false;
      this.localBal = 0;
      this.remoteBal = 0;
      data.channels.filter(channel => {
        if (undefined !== channel.local_balance) {
          this.localBal = +this.localBal + +channel.local_balance;
        }
        if (undefined !== channel.remote_balance) {
          this.remoteBal = +this.remoteBal + +channel.remote_balance;
        }
        if (channel.active === true) {
          this.activeChannels++;
        } else {
          this.inactiveChannels++;
        }
      });
      if (undefined === this.localBal || null == this.localBal) {
        this.localBal = 0;
      }
      if (undefined === this.remoteBal || null == this.remoteBal) {
        this.remoteBal = 0;
      }
      this.totalLocalBalance = [...[{name: 'Local Balance', value: this.localBal}]];
      this.totalRemoteBalance = [...[{name: 'Remote Balance', value: this.remoteBal}]];
      this.maxBalanceValue = (this.localBal > this.remoteBal) ? this.localBal : this.remoteBal;
    });
    this.rtlService.getChannels('pending', '')
    .pipe(takeUntil(this.channelSub[2]))
    .subscribe((data: any) => {
      this.flgLoading[2] = false;
      this.pendingChannels = 0;
      this.pendingChannels = (undefined === data.pending_open_channels) ? 0 : data.pending_open_channels.length;
      this.logger.info(this.pendingChannels);
    },
    (err: Error) => {
      this.logger.error(err);
      this.flgLoading[2] = 'error';
    });
  }

  ngOnDestroy() {
    this.channelSub.forEach(completeSub => {
      completeSub.next();
      completeSub.complete();
    });
  }
}
